.detail-pokemon-page__card-info{display:flex;flex-flow:nowrap column;justify-content:normal;align-items:center;padding-top:80px}.detail-pokemon-page .origin{display:flex;flex-flow:nowrap row;justify-content:center;align-items:center;position:relative;width:220px;height:75px;margin:20px 0;overflow:hidden;border-radius:50px}.detail-pokemon-page .origin--original{cursor:default;color:#783205;font:700 25px "Skranji",sans-serif;text-shadow:0 2px 2px #fae385;text-align:center;background-size:300% 100%;background-image:linear-gradient(160deg, #c5814a, #b47e11, #fef1a2, #bc881b, #a54e07);border:1px solid #a55d07;box-shadow:0 3px 6px rgba(0,0,0,.16),0 3px 6px rgba(110,80,20,.4),inset 0 -2px 5px 1px #8b4208,inset 0 -1px 1px 3px #fae385}.detail-pokemon-page .origin--original:before{content:"";position:absolute;top:0;width:30px;height:100%;background:rgba(255,255,255,.9);filter:blur(30px);transform:translateX(-140px);transition:transform .6s ease-out}.detail-pokemon-page .origin--original:hover:before{transform:translateX(300px)}.detail-pokemon-page .origin--pokefan{background-color:#e9f2de}.detail-pokemon-page .origin--pokefan .pokefan{display:flex;flex-flow:nowrap row;justify-content:normal;align-items:center;position:relative;overflow:hidden;cursor:pointer;min-width:50%;height:100%;transition:min-width .3s linear 0s}.detail-pokemon-page .origin--pokefan .pokefan svg{position:absolute;z-index:1;font-size:50px;transition:color .4s cubic-bezier(0.25, 0.1, 0.25, 1),left .4s cubic-bezier(0.25, 0.5, 0, 1),right .4s cubic-bezier(0.25, 0.5, 0, 1)}.detail-pokemon-page .origin--pokefan .pokefan:before,.detail-pokemon-page .origin--pokefan .pokefan:after{position:absolute;text-align:center;color:#e9f2de;font:700 18px "Merriweather Sans",sans-serif}.detail-pokemon-page .origin--pokefan .pokefan:hover{min-width:330px}.detail-pokemon-page .origin--pokefan .pokefan:hover svg,.detail-pokemon-page .origin--pokefan .pokefan:hover:before,.detail-pokemon-page .origin--pokefan .pokefan:hover:aft