r .6s ease}.detail-pokemon-page .hall-Of-fame__upload-btn:hover{background:#476b6a;color:#e9f2de;box-shadow:0 0 0 3px #e9f2de;border-radius:100px}.detail-pokemon-page .hall-Of-fame__no-content{text-align:center;color:#324ff1;margin:25px auto;padding:0 10px}.detail-pokemon-page .hall-Of-fame__no-content img{width:clamp(150px,30vw,300px);margin:20px auto}.detail-pokemon-page .hall-Of-fame__no-content p{font:700 clamp(2rem,1.5rem + 1.5vw,4rem) "Merriweather Sans",sans-serif}.detail-pokemon-page .hall-Of-fame__no-content p:last-child{color:#cf3030}